import Vue from 'vue'
import Router from 'vue-router'
import helloPage from '@/components/HelloWorld.vue'
import indexPage from '@/components/indexPage.vue'
import loginPage from '@/components/loginPage.vue'
import registerPage from '@/components/registerPage.vue'
import orderPage from '@/components/orderPage.vue'
import productDetail from '@/components/productDetail.vue'
import orderDetail from '@/components/orderDetail.vue'
import selfPage from '@/components/self.vue'
import productsPage from '@/components/productsPage.vue'
import createOrderPage from '@/components/createOrder.vue'
import orderCreated from '@/components/orderCreated.vue'
import orderCanceled from '@/components/orderCanceled.vue'
import createReset from '@/components/createResetPage.vue'
import resetPage from '@/components/resetPage.vue'


// vueRouter
Vue.use(Router)

/*
routesMap
Mappath

{
  path:'URLindex/index',
  name:'Path',
  component: indexindexPage.vueindexPage
}
*/
const routes = [
  {
    path: "/hello",
    name: "helloPage",
    component: helloPage
  },
  {
    path: "/index",
    name: "indexPage",
    component: indexPage,
    meta: {
      title: ''
    },
  },
  {
    path: "/login",
    name: "loginPage",
    component: loginPage,
    meta: {
      title: ''
    },
  },
  {
    path: "/register",
    name: "registerPage",
    component: registerPage,
    meta: {
      title: ''
    }
  },
  {
    path: "/order",
    name: "orderPage",
    component: orderPage,
    meta: {
      title: ''
    },
  },
  {
    path: "/productDetail/:pid",
    name: "productDetailPage",
    component: productDetail,
    meta:{
      title: ''
    }
  },
  {
    path: "/orderDetail/:oid",
    name: "orderDetailPage",
    component: orderDetail,
    meta: {
      title: ""
    }
  },
  {
    path: "/self",
    name: "selfPage",
    component:selfPage,
    meta: {
      title: ""
    }
  },
  {
    path: "/products/:cid",
    name: "productsPage",
    component: productsPage,
    meta: {
      title: ""
    }
  },
  {
    path: "/createorder",
    name: "createOrderPage",
    component: createOrderPage,
    meta: {
      title: ""
    }
  },
  {
    path: "/orderCreated",
    name:"createPaypalPage",
    component: orderCreated,
    meta: {
      title: ""
    }
  },
  {
    path: "/orderCanceled",
    name: "cancalOrderPage",
    component: orderCanceled,
    meta: {
      title: ""
    }
  },
  {
    path: "/createreset",
    name: "createResetPage",
    component: createReset,
    meta: {
      title: ""
    }
  },
  {
    path: "/reset",
    name: "resetPage",
    component: resetPage,
    meta: {
      title: ""
    }
  }
]

/*
Router
base
routes
*/
var router = new Router({
  routes
})

router.beforeEach((to,from,next) => {
  if(to.meta.title){
    document.title = to.meta.title
  }
  next()
})

/*
index.jsrouter
*/
export default router
